@use "../abstracts/" as *;

@font-face {
  font-family: "Roslindale";
  src: url(../assets/fonts/Roslindale/Roslindale/roslindale-djr-specimen.pdf);
}

body {
  font-family: $Second-font;
  font-weight: 400;
  font-size: 1rem;
  line-height: pxToRem(24);
  color: $text-color;
  background-color: $background-color;

  @include tablet {
    font-size: pxToRem(11);
    line-height: pxToRem(16);
  }
}
